import { Component, OnInit } from '@angular/core';
import { SwiperOptions } from 'swiper';

@Component({
  selector: 'app-capacity-building',
  templateUrl: './capacity-building.component.html',
  styleUrls: ['./capacity-building.component.css']
})
export class CapacityBuildingComponent implements OnInit {

  config: SwiperOptions = {};

  constructor() { }

  ngOnInit(): void {
    this.config  = {
      effect: 'coverflow',
      grabCursor: true,
      centeredSlides: true,
      slidesPerView: 'auto',
      coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
      },
      pagination: {
          el: '.scopes.swiper-pagination',
          clickable: true
      },
      navigation: {
          nextEl: '.scopes.swiper-button-next',
          prevEl: '.scopes.swiper-button-prev',
      },
      spaceBetween: 30
    };
  }

}
